import React, { useContext } from "react";
import SelectList from "components/SelecList/";
import ScoreTitle from "components/ScoreTitle";
import ScoreBar from "components/ScoreBar";
import FinishPage from "FinishPage";
import { getLevelData } from "helper/helpers";
import { PageContext } from "contexts/ImageList.context";
import {
  createMuiTheme,
  ThemeProvider,
  makeStyles,
} from "@material-ui/core/styles";

const overrides = (mainColor) => {
  return {
    MuiButton: {
      // Name of the rule
      root: {
        fontFamily: '"8-Bit-Madness"',
        borderRadius: 0,
        textTransform: "none",
        fontSize: "1.1rem",
      },
      // contained: {
      //   color: "white",
      // },
      outlinedPrimary: {
        // Some CSS
        border: "0",
        outline: `2px solid ${mainColor}`,
        outlineOffset: "-2px",
        // borderWidth: "2px",
        "&:hover": {
          border: "0",
          outline: `2px solid ${mainColor}`,
          outlineOffset: "-2px",
          backgroundColor: "rgba(41, 80, 251, 0.1)",
        },
      },
    },
  };
};
const themeBeginner = createMuiTheme({
  overrides: overrides("#2950FB"),
  palette: {
    primary: {
      main: "#2950FB",
    },
  },
});

const themeHard = createMuiTheme({
  overrides: overrides("#A35FF9"),
  palette: {
    primary: {
      main: "#A35FF9",
    },
  },
});
const themeMaster = createMuiTheme({
  overrides: overrides("#E92985"),
  palette: {
    primary: {
      main: "#E92985",
    },
  },
});

const useStyles = makeStyles({
  pageContent: {
    maxWidth: " 480px",
    display: "flex",
    flexDirection: "column",
  },
});

export default function PageContent(props) {
  const pageState = useContext(PageContext);
  const classes = useStyles();
  // const socer =pageState.score;
  const levelData = getLevelData(pageState);
  const getTheme = () => {
    switch (levelData.level) {
      case 1:
        return themeBeginner;
      case 2:
        return themeHard;
      case 3:
        return themeMaster;
      default:
        return themeBeginner;
    }
  };
  const questionsNum = () => {
    switch (levelData.level) {
      case 1:
        return pageState.levelNum.beginnerNum;

      case 2:
        return pageState.levelNum.hardNum;
      case 3:
        return pageState.levelNum.masterNum;
      default:
        return console.log("levelData.level", levelData.level);
    }
  };
  return (
    <div className={classes.pageContent}>
      <ThemeProvider
        // theme={levelData.level === 1 ? themeBeginner : themeHard}
        theme={getTheme()}
      >
        {!pageState.isFinished ? (
          <>
            <ScoreTitle {...pageState} />
            <SelectList />
            <ScoreBar
              answeredNum={levelData.currentNumInLevel}
              level={levelData.levelName}
              questionsNum={questionsNum()}
            />
          </>
        ) : (
          <FinishPage
            score={pageState.score}
            scoreSum={Object.values(pageState.levelNum).reduce(
              (a, b, i) => b * ((i + 1) * 100) + a,
              0
            )}
          />
        )}
      </ThemeProvider>
    </div>
  );
}
